<template>
  <div class="main-container">
    <!--   -->
    <article class="content">
      <!--   -->
      <search-bar v-if="showSearch" @search="search" />

      <!-- MainHeader  -->
      <HeroSection />

      <!-- Best Topics  -->
      <best-topic-section />

      <!--    -->
      <recent-post-section />
    </article>

    <!--    -->
  </div>
</template>

<script>
import HeroSection from '@/components/home/HeroSection.vue';
import SearchBar from '@/components/home/SearchBar.vue';
import BestTopicSection from '@/components/home/BestTopicSection.vue';
import RecentPostSection from '@/components/home/RecentPostSection.vue';
import axios from 'axios';

export default {
  components: {
    HeroSection,
    SearchBar,
    BestTopicSection,
    RecentPostSection
  },
  data() {
    return {
      categories: ['', '', '', '', '', ''],
      selectedCategory: '',  //  
      bestTopics: [], //   Best Topics
      recentArticles: [],  //    
      page: 0, //   (   )
      size: 5, //     
      categoryMap: {
        "vue": "",
        "react": "",
        "angular": "",
        "economy": "",
        "entertainment": "",
        "international": "",
        "": ""
      }
    };
  },
  computed: {
    filteredArticles() {
      if (Array.isArray(this.recentArticles) && this.recentArticles.length > 0) {
        const categoryToCompare = this.selectedCategory.toLowerCase().trim();  // ,  
        if (this.selectedCategory === '') {
          return this.recentArticles;
        } else {
          return this.recentArticles.filter(article =>
              article.category && article.category.name.toLowerCase().trim() === categoryToCompare
          );
        }
      }
      return [];  //      
    }
  },
  methods: {
    //    
    filterCategory(category) {
      this.selectedCategory = category;
      this.fetchRecentPosts();  //      
    },

    //   ( alert  )
    search(query) {
      alert(`: ${query}`);
    },

    async fetchBestTopics() {
      try {
        const res = await axios.get(`${process.env.VUE_APP_API_URL}/posts`, {
          params: { page: 0, size: 100 }
        });

        const posts = res.data.content;

        const ratioPromises = posts.map(post =>
            axios.get(`${process.env.VUE_APP_API_URL}/feedback/${post.id}/ratio`)
                .then(ratioRes => ({
                  ...post,
                  ratio: ratioRes.data
                }))
                .catch(() => null)
        );

        const postsWithRatio = (await Promise.all(ratioPromises)).filter(Boolean);

        const filtered = postsWithRatio.filter(p =>
            Math.abs(p.ratio.trueNewsRatio - 0.5) <= 0.05
        );

        filtered.sort((a, b) =>
            Math.abs(a.ratio.trueNewsRatio - 0.5) - Math.abs(b.ratio.trueNewsRatio - 0.5)
        );

        this.bestTopics = filtered.slice(0, 3); //  10  .slice(0, 10)
      } catch (error) {
        console.error("Best Topics  ", error);
      }
    },

    //     
    async fetchRecentPosts() {
      if (this.selectedCategory === ""){
        try {
          const response = await axios.get(`${process.env.VUE_APP_API_URL}/posts`, {
            params: {
              page: this.page, //  
              size: this.size, //     
            },
          });
          this.recentArticles = response.data.content; //    
        } catch (error) {
          console.error("   .", error);
        }
      } else {
        try {
          // const encodedCategory = encodeURIComponent(this.selectedCategory);
          const url = `${process.env.VUE_APP_API_URL}/posts/category/${this.selectedCategory}`;
          console.log("Request URL:", url); //  URL  
          const response = await axios.get(url, {
            params: {
              page: this.page,
              size: this.size,
            },
          });
          console.log(response.data);
          this.recentArticles = response.data;
          console.log("Fetched articles:", this.recentArticles); //   
        } catch (error) {
          console.error("    .", error);
        }
      }
    }
  },
  created() {
    this.fetchRecentPosts();  //     
    this.fetchBestTopics();
  }
};
</script>

<style scoped>
.main-container {
  display: flex;
  justify-content: space-between;
}

.content {
  width:100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

/*   */
@media (max-width: 1024px) {
  .main-container {
    flex-direction: column;
    padding: 0 16px;
  }
}
</style>