<template>
  <section class="best-topics-section">
    <div class="section-container">
      <SectionHeader
          title="   "
          subtitle="     "
          linkText="  "
          linkTo="/best-topics"
      />

      <!--   -->
      <div v-if="loading" class="loading-state">
        <div class="loading-spinner"></div>
        <p>   ...</p>
      </div>

      <!--   -->
      <div v-else-if="error" class="error-state">
        <svg xmlns="http://www.w3.org/2000/svg" width="48" height="48" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
          <circle cx="12" cy="12" r="10"></circle>
          <line x1="15" y1="9" x2="9" y2="15"></line>
          <line x1="9" y1="9" x2="15" y2="15"></line>
        </svg>
        <h3 class="error-title">   </h3>
        <p class="error-message">     .</p>
        <button @click="fetchBestTopics" class="retry-button"> </button>
      </div>

      <!--    -->
      <div v-else class="topics-grid">
        <TopicCard
            v-for="article in bestTopics"
            :key="article.id"
            :article="article"
        />
        <!--    -->
        <div v-if="bestTopics.length === 0" class="empty-state">
          <svg xmlns="http://www.w3.org/2000/svg" width="48" height="48" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
            <path d="M14 2H6a2 2 0 0 0-2 2v16a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V8z"></path>
            <polyline points="14 2 14 8 20 8"></polyline>
            <line x1="16" y1="13" x2="8" y2="13"></line>
            <line x1="16" y1="17" x2="8" y2="17"></line>
          </svg>
          <h3 class="empty-title"> </h3>
          <p class="empty-message">   .</p>
        </div>
      </div>

      <!--   -->
      <div v-if="bestTopics.length > 0" class="load-more-container">
        <router-link to="/best-topics" class="load-more-button">
          <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
            <polyline points="9 18 15 12 9 6"></polyline>
          </svg>
            
        </router-link>
      </div>
    </div>
  </section>
</template>

<script>
import axios from 'axios';
import SectionHeader from '@/components/home/SectionHeader.vue';
import TopicCard from '@/components/topic/TopicCard.vue';
import { getCategoryByContent } from '@/utils/CategoryUtils';

export default {
  name: 'BestTopicsSection',
  components: {
    SectionHeader,
    TopicCard
  },
  data() {
    return {
      bestTopics: [],
      loading: false,
      error: null
    };
  },
  created() {
    this.fetchBestTopics();
  },
  methods: {
    async fetchBestTopics() {
      this.loading = true;
      this.error = null;

      try {
        //     
        const res = await axios.get(`${process.env.VUE_APP_API_URL}/posts`, {
          params: {
            page: 0,
            size: 100
          }
        });

        console.log('API  :', res.data);

        const posts = res.data.content || res.data;

        //       
        posts.forEach(post => {
          console.log(` ID ${post.id}:`, {
            title: post.title,
            category: post.category,
            categoryType: typeof post.category,
            autoCategory: this.getCategoryName(post)
          });

          //    -   categoryName  
          post.categoryName = this.getCategoryName(post);
        });

        //     
        const ratioPromises = posts.map(post =>
            axios.get(`${process.env.VUE_APP_API_URL}/feedback/${post.id}/ratio`)
                .then(ratioRes => ({
                  ...post,
                  ratio: ratioRes.data
                }))
                .catch(error => {
                  console.warn(` ${post.id}    :`, error);
                  return null;
                })
        );

        const postsWithRatio = (await Promise.all(ratioPromises)).filter(Boolean);

        //     ( 0.5  )
        const controversialPosts = postsWithRatio.filter(post => {
          if (!post.ratio || typeof post.ratio.trueNewsRatio !== 'number') {
            return false;
          }
          return Math.abs(post.ratio.trueNewsRatio - 0.5) <= 0.05;
        });

        //     (0.5  )
        controversialPosts.sort((a, b) =>
            Math.abs(a.ratio.trueNewsRatio - 0.5) - Math.abs(b.ratio.trueNewsRatio - 0.5)
        );

        this.bestTopics = controversialPosts.slice(0, 3);

        console.log('  :', this.bestTopics);
        console.log(' :', this.bestTopics.map(post => ({
          id: post.id,
          title: post.title,
          categoryName: post.categoryName,
          autoCategory: getCategoryByContent(post.title, post.content)
        })));

      } catch (error) {
        console.error("Best Topics  :", error);
        this.error = error;
      } finally {
        this.loading = false;
      }
    },

    getCategoryName(article) {
      //   
      console.log('  :', {
        articleId: article.id,
        category: article.category,
        categoryType: typeof article.category
      });

      // 1. API     
      if (article.categoryName) {
        return article.categoryName;
      }

      // 2.    
      if (article.category) {
        //   name   
        if (typeof article.category === 'object' && article.category.name) {
          return article.category.name;
        }

        //   
        if (typeof article.category === 'string') {
          return article.category;
        }
      }

      // 3.   
      const autoCategory = getCategoryByContent(article.title, article.content);
      console.log(` ID ${article.id}  :`, autoCategory);
      return autoCategory;
    }
  }
}
</script>

<style scoped>
/*    */
.best-topics-section {
  padding: 80px 0;
  background: white;
}

.section-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/*    */
.topics-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 24px;
  margin-top: 48px;
}

/*   */
.loading-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 60px 20px;
  color: #64748b;
  text-align: center;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 3px solid #f1f5f9;
  border-top: 3px solid #3662E3;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 16px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/*   */
.error-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 60px 20px;
  text-align: center;
}

.error-state svg {
  color: #ef4444;
  margin-bottom: 16px;
}

.error-title {
  font-size: 18px;
  font-weight: 600;
  color: #1e293b;
  margin: 0 0 8px 0;
}

.error-message {
  font-size: 14px;
  color: #64748b;
  margin: 0 0 20px 0;
}

.retry-button {
  padding: 10px 20px;
  background-color: #3662E3;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.retry-button:hover {
  background-color: #2952CC;
}

/*   */
.empty-state {
  grid-column: 1 / -1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 60px 20px;
  text-align: center;
}

.empty-state svg {
  color: #94a3b8;
  margin-bottom: 16px;
}

.empty-title {
  font-size: 18px;
  font-weight: 600;
  color: #1e293b;
  margin: 0 0 8px 0;
}

.empty-message {
  font-size: 14px;
  color: #64748b;
  margin: 0;
}

/*   */
.load-more-container {
  text-align: center;
  margin-top: 40px;
}

.load-more-button {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 12px 24px;
  background-color: #f8fafc;
  color: #64748b;
  text-decoration: none;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  transition: all 0.2s ease;
  border: 1px solid #e2e8f0;
}

.load-more-button:hover {
  background-color: #f1f5f9;
  color: #3662E3;
  border-color: #3662E3;
  transform: translateY(-1px);
}

/*   */
@media (max-width: 768px) {
  .topics-grid {
    grid-template-columns: 1fr;
    margin-top: 32px;
  }

  .best-topics-section {
    padding: 60px 0;
  }
}

@media (max-width: 480px) {
  .section-container {
    padding: 0 16px;
  }

  .best-topics-section {
    padding: 40px 0;
  }

  .topics-grid {
    gap: 16px;
  }
}
</style>